*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 21px;
  font-family: "ProximaNova", "Arial", sans-serif;
  color: #1f1f1f; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  /* 1 */
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%); }

.container {
  max-width: 1004px;
  padding-right: 12px;
  padding-left: 12px;
  margin-right: auto;
  margin-left: auto; }

@media (max-width: 800px) {
  .container {
    padding-right: 21px;
    padding-left: 21px; } }

@media (max-width: 767px) {
  .container {
    padding-right: 16px;
    padding-left: 16px; } }

.header-top__wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px; }

.header-top__social {
  display: flex; }

.header-top__social-link {
  width: 31px;
  height: 31px;
  border: 1px solid #c2c2c2;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s; }

.header-top__social-link:hover {
  background-color: #1f1f1f;
  opacity: 0.2; }

.header-top__social-img--fb {
  width: 8px;
  height: 15px;
  background: url("../img/css_sprites.png") -177px -10px; }

.header-top__social-img--vk {
  width: 11px;
  height: 13px;
  background: url("../img/css_sprites.png") -10px -113px; }

.header-top__social-img--twitter {
  width: 11px;
  height: 13px;
  background: url("../img/css_sprites.png") -72px -113px; }

.header-top__social-img--google {
  width: 14px;
  height: 12px;
  background: url("../img/css_sprites.png") -10px -81px; }

.header-top__contact-card {
  width: 700px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.header-top__wrapper-top__city {
  position: relative; }

.header-top__wrapper-top__city::before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 11px;
  height: 16px;
  background: url("../img/css_sprites.png") -116px -10px; }

.header-top__city {
  cursor: pointer;
  border: none;
  appearance: none;
  padding-left: 19px;
  font-size: 14px;
  font-family: "ProximaNovaCondenced", "Arial", sans-serif;
  font-weight: 500;
  line-height: 1.286;
  overflow: hidden; }

.header-top__city-change {
  margin-right: 10px;
  border-bottom: 1px dashed #c2c2c2; }

.header-top__city::after {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 6px;
  background: url("../img/css_sprites.png") -147px -81px; }

.header-top__contact-phone {
  position: relative;
  font-size: 17px;
  font-family: "ProximaNovaCondenced", "Arial", sans-serif;
  text-transform: uppercase;
  line-height: 1.059;
  color: #1f1f1f;
  padding-left: 17px; }

.header-top__contact-phone::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 13px;
  height: 15px;
  background: url("../img/css_sprites.png") -82px -46px; }

.header-top__contact-inner {
  position: relative;
  font-family: "ProximaNovaCondenced", "Arial", sans-serif;
  color: #1f1f1f;
  font-size: 15px;
  line-height: 1.2;
  padding-left: 20px; }

.header-top__contact-inner::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 14px;
  height: 16px;
  background: url("../img/css_sprites.png") -82px -10px; }

.header-top__contact-cart {
  position: relative;
  font-family: "ProximaNovaCondenced", "Arial", sans-serif;
  color: #1f1f1f;
  font-size: 15px;
  line-height: 1.2;
  padding-left: 25px;
  padding-right: 10px; }

.header-top__contact-cart::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 17px;
  height: 15px;
  background: url("../img/css_sprites.png") -10px -46px; }

.header-top__contact-cart-counter {
  font-size: 14px;
  font-family: "PTSerif", "Arial", sans-serif;
  color: #b98348;
  font-style: italic;
  line-height: 1.286;
  border-left: 1px solid #b98348;
  padding-left: 10px;
  margin-left: 10px; }

.header-bottom {
  background-color: #1f1f1f; }

.header-bottom__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 10px; }

.header-bottom__logo {
  display: flex; }

.header-bottom__nav {
  width: 715px; }

.header-bottom__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 100%; }

.header-bottom__items {
  text-decoration: none;
  margin-right: 20px; }

.header-bottom__item {
  color: #ffffff;
  text-transform: uppercase;
  font-family: "ProximaNovaCondenced", "Arial", sans-serif;
  font-size: 15px;
  line-height: 1.2; }

.header-bottom__item--checked {
  border-bottom: 1px dashed #ffffff; }
